.top-0{
  top: 0px;
}

.m-auto{
  margin: auto;
}

.mx-auto{
  margin-left: auto;
  margin-right: auto;
}

.mr-auto{
  margin-right: auto;
}

.mt-3{
  margin-top: 0.75rem;
}

.flex{
  display: flex;
}

.hidden{
  display: none;
}

.h-screen{
  height: 100vh;
}

.h-3\/4{
  height: 75%;
}

.h-auto{
  height: auto;
}

.min-h-full{
  min-height: 100%;
}

.w-full{
  width: 100%;
}

.min-w-full{
  min-width: 100%;
}

.resize{
  resize: both;
}

.flex-row{
  flex-direction: row;
}

.overflow-y-hidden{
  overflow-y: hidden;
}

.border-r-8{
  border-right-width: 8px;
}

.border-solid{
  border-style: solid;
}

.border-black{
  --tw-border-opacity: 1;
  border-color: rgb(0 0 0 / var(--tw-border-opacity));
}

.bg-color3{
  --tw-bg-opacity: 1;
  background-color: rgb(16 29 64 / var(--tw-bg-opacity));
}

.bg-color1{
  --tw-bg-opacity: 1;
  background-color: rgb(25 51 90 / var(--tw-bg-opacity));
}

.bg-white{
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
}

.p-4{
  padding: 1rem;
}

.text-left{
  text-align: left;
}

.text-center{
  text-align: center;
}

.align-top{
  vertical-align: top;
}

.text-xl{
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.font-semibold{
  font-weight: 600;
}

.font-bold{
  font-weight: 700;
}

.text-white{
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.text-color2{
  --tw-text-opacity: 1;
  color: rgb(143 200 235 / var(--tw-text-opacity));
}

.shadow-md{
  --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.shadow-color2\/30{
  --tw-shadow-color: rgb(143 200 235 / 0.3);
  --tw-shadow: var(--tw-shadow-colored);
}

*{
  box-sizing: border-box;
  text-decoration: none;
}

textarea{
  resize:horizontal;
}

nav {
  display: flex;
  align-items: center;
  background-color: #21222A;
  height: 30px;
  padding: 30px 25px;
}

.nav--log_text , .nav--title{
  margin: 0;
}

.nav--title,.css,.html{
  color: #DEEBF8;
  font-weight: 600;
}

.nav--log_text {
  margin-right: auto;
  color: #61DAFB;
  font-weight: 700;
  font-size: 22px;
}

.nav-icon{
  height: 30px;
  margin-right: 7px;
}

.h{
  height:-webkit-max-content ;
  height:max-content ;
}

#css:checked ~ .css{
  color: #61DAFB;
  font-weight:900;
}

#html:checked ~ .html{
  color: #61DAFB;
  font-weight:900;
}

.checked\:hidden:checked{
  display: none;
}

.hover\:text-white:hover{
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}